/* rem support Func
 -------------------------- */
.y-scroll {
  overflow: hidden; }
  .y-scroll__content.is-dropped {
    transition: 200ms;
    -webkit-transition: 200ms; }
  .y-scroll__top {
    text-align: center;
    height: 0.6rem;
    line-height: 0.6rem; }
  .y-scroll__bottom {
    text-align: center;
    height: 0.6rem;
    line-height: 0.6rem; }
    .y-scroll__bottom .iconfont {
      height: 0.6rem;
      width: 0.6rem;
      vertical-align: sub; }
    .y-scroll__bottom .icon-img {
      height: 0.4rem;
      width: 0.4rem;
      vertical-align: middle; }
  .y-scroll__text {
    vertical-align: middle; }
  .y-scroll--load .iconfont {
    height: 0.6rem;
    width: 0.6rem;
    vertical-align: sub; }
  .y-scroll--load .icon-img {
    height: 0.4rem;
    width: 0.4rem;
    vertical-align: middle; }
