/* rem support Func
 -------------------------- */
.y-tabBar {
  height: 0.9rem;
  padding-top: 0.14rem;
  background: #FFFFFF;
  text-align: center;
  border-top: 1px solid #E7E7EA; }
  .y-tabBar__child {
    vertical-align: middle;
    font-size: 0.22rem;
    position: relative; }
  .y-tabBar__img {
    height: 0.44rem;
    width: 0.44rem;
    vertical-align: middle; }
  .y-tabBar__text {
    text-align: center;
    margin-top: 0.03rem;
    position: relative; }
    .y-tabBar__text--big {
      font-size: 0.3rem;
      line-height: 0.6rem; }
  .y-tabBar__redpoint {
    position: relative;
    display: inline-block;
    background: #fd1d1d;
    width: 0.14rem;
    height: 0.14rem;
    top: -0.25rem;
    left: -0.1rem;
    border-radius: 0.2rem; }
