 -------------------------- */
.y-toast__panel {
  min-width: 3rem;
  line-height: 2;
  color: #fff;
  font-size: 0.28rem;
  padding: 0.08rem 0rem;
  background-color: #333;
  border: solid 1px #666;
  border-radius: 0.1rem;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  opacity: 0.8;
  -webkit-transition: all .5s;
  transition: all .5s;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-box;
  display: -webkit-flex;
  z-index: 101; }

.y-toast__mask {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 100; }

.y-toast__content {
  padding: 0 0.4rem;
  font-size: 0.28rem;
  text-align: center;
  -ms-flex: 1;
      flex: 1;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  color: #fff; }

.y-toast__icon {
  font-size: 0.28rem;
  vertical-align: middle; }

.y-toast__close {
  font-size: 0.28rem;
  position: absolute;
  right: 0.08rem;
  top: 0.08rem;
  font-weight: bold;
  padding: 0 0rem;
  cursor: pointer; }

.toast-fade-enter, .toast-fade-leave-to {
  opacity: 0;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s; }
  .toast-fade-enter .y-toast__panel, .toast-fade-leave-to .y-toast__panel {
    transform: translate(-50%, -80%);
    -webkit-transform: translate(-50%, -80%); }
