 -------------------------- */
.y-steps {
  width: 6.9rem;
  position: relative;
  -webkit-transition: 0.3s all;
  transition: 0.3s all; }
  .y-steps__bg {
    height: 0.12rem;
    position: relative; }
  .y-steps__child {
    text-align: center;
    position: relative; }
  .y-steps__ball {
    width: 0.22rem;
    height: 0.22rem;
    border-radius: 0.22rem;
    background: #E7E7EA;
    position: absolute;
    left: 50%;
    margin-left: -0.11rem;
    top: -0.06rem;
    z-index: 3; }
    .y-steps__ball--current {
      background: #6DC054; }
  .y-steps__bg2 {
    width: 100%;
    background: #E7E7EA;
    height: 0.12rem;
    border-radius: 0.12rem;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 0; }
  .y-steps__currentBg {
    width: 30%;
    background: #6DC054;
    height: 0.12rem;
    border-radius: 0.12rem;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1; }
  .y-steps__currentTips {
    position: relative;
    width: 0.55rem;
    height: 0.55rem;
    background-size: 100% 100%;
    margin-left: -0.275rem;
    top: 0rem;
    margin-bottom: 0.1rem; }
  .y-steps__text {
    position: relative;
    text-align: center;
    top: 0.32rem; }
