.y-swiper {
  overflow: hidden;
  position: relative;
  height: 100%; }
  .y-swiper.fullWarp {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    color: #fff;
    font-size: 30px;
    text-align: center;
    background: #000000;
    z-index: 9999; }
  .y-swiper__wrap {
    position: relative;
    overflow: hidden;
    height: 100%;
    -webkit-transform: translateZ(0);
    transform: translateZ(0); }
    .y-swiper__wrap > div {
      position: absolute;
      -webkit-transform: translateX(-100%);
      transform: translateX(-100%);
      width: 100%;
      height: 100%;
      display: none; }
    .y-swiper__wrap > div.is-active {
      display: block;
      -webkit-transform: none;
      transform: none; }

.y-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  width: 100%;
  padding: 0 0.4rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }
  .y-btn > div {
    border-radius: 50%;
    background: rgba(0, 0, 0, 0.6);
    color: rgba(255, 255, 255, 0.6);
    text-align: center; }
    .y-btn > div .icon-jiantou:before {
      display: block; }
    .y-btn > div .icon-jiantou.arrowleft:before {
      display: block;
      transform: rotate(180deg);
      -webkit-transform: rotate(180deg); }
  .y-btn__icon {
    display: block;
    height: inherit;
    line-height: inherit;
    font-size: 0.4rem; }
  .y-btn__left {
    width: 0.8rem;
    height: 0.8rem;
    line-height: 0.8rem;
    float: left; }
  .y-btn__right {
    height: 0.8rem;
    width: 0.8rem;
    line-height: 0.8rem;
    float: right; }

.y-indicators {
  position: absolute;
  bottom: 0.1rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%); }
  .y-indicators__spot {
    width: 0.13rem;
    height: 0.13rem;
    display: inline-block;
    border-radius: 100%;
    background: #D7D7D7;
    margin: 0 0.03rem; }
    .y-indicators__spot.active {
      background: #E9443C; }

.y-page {
  position: absolute;
  right: 0.2rem;
  top: 0.2rem;
  font-size: 0.2rem;
  background: rgba(0, 0, 0, 0.6);
  color: #ffffff;
  padding: 0.05rem 0.15rem;
  border-radius: 0.1rem; }
  .y-page.fullPage {
    right: 50%;
    transform: translate(50%, 0);
    -webkit-transform: translate(50%, 0); }

.y-close {
  position: absolute;
  right: 0.2rem;
  top: 0.2rem;
  font-size: 0.2rem;
  background: rgba(0, 0, 0, 0.6);
  color: #ffffff;
  padding: 0.05rem 0.05rem;
  border-radius: 50%; }
