.y-swiperitem.fullOver {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch; }
  .y-swiperitem.fullOver img {
    width: auto;
    max-width: 100%;
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
